@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&family=Skranji&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

.navbar {
  font-size: 30px;
  color: white;
  text-align: center;
  font-family: "Skranji", cursive;
}

.App {
  height: 130vh;
  width: 100vw;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
}

.Score-card {
  color: white;
  height: 70px;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 30px;
  gap: 30px;
  font-size: 15px;
  font-family: "Press Start 2P", cursive;
  background-color: rgba(10, 10, 10, 0.35);
}

.cards {
  color: whitesmoke;
}

.cards {
  background-color: rgba(0, 0, 0, 0.6);
  height: 700px;
  width: 1000px;
  display: flex;
  grid-gap: 2%;
  gap: 2%;
  justify-content: center;
  flex-wrap: wrap;
  margin: 20px auto;
}

.card {
  width: auto;
  flex-basis: 200px;
  height: auto;
  overflow: hidden;
  box-shadow: 0px 0px 20px 0px rgba(255, 255, 255, 1);
}

.card img {
  height: 220px;
  width: 200px;
  border-radius: 5px;
  transition: all 0.5s ease-in-out;
}

.card img:hover {
  height: 230px;
  width: 210px;
}

